// Normalize Styles
@import "node_modules/normalize.css/normalize";

// Import all needed help
@import 'common/_variables';
@import 'common/_mixin';
@import 'common/_material-mixin';
@import 'common/_helpers';
@import 'common/_fonts';
@import 'common/_base';
@import 'common/_grid';
@import 'common/_material-design';
@import "common/_mCustomScrollbar";

// Import Modules
@import "../_modules/link/link";
@import "../_modules/standard-banner/standard-banner";
@import "../_modules/primary-nav/primary-nav";

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.skip-links {
    position: absolute;
    z-index: 99;
    background-color: #fff;
}

// Styling on warning for users on IE7 or below
.browsehappy {
    text-align: center;
    margin: 0;
    background: #fff8e2;
    color: #000;
    padding: 0.5em 0;
}


.timetable {
    padding-top: $gutterTablet;

    [class^="col-"], [class*=" col-"] {
        float: left;
    }

    .col-1 { width: 8.333333%; }
    .col-2 { width: 16.666667%; }
    .col-3 { width: 25%; }
    .col-4 { width: 33.333333%; }
    .col-5 { width: 41.666667%; }
    .col-6 { width: 50%; }
    .col-7 { width: 58.333333%; }
    .col-8 { width: 66.666667%; }
    .col-9 { width: 75%; }
    .col-10 { width: 83.333333%; }
    .col-11 { width: 91.666667%; }
    .col-12 { width: 100%; }
}

.card {
    @include reset-transitions();
    @include material-shadow(1);

    background-color: $white;
    margin-bottom: $gutter;
    overflow: hidden;
    padding: 0 $gutter;

    h2 {
        margin: $gutter 0;
        text-align: center;
    }

    p {
        font-weight: $bold;
    }

    .ripple {
        circle {
            fill: $grey;
        }
    }

    .loader {
        -webkit-transform: none !important;
          -moz-transition: none !important;
            -ms-transform: none !important;
            -o-transition: none !important;
                transform: none !important;
        bottom: 0;
        height: 40px !important;
        left: auto;
        margin: auto;
        position: absolute;
        right: 20px;
        top: 0;
        width: 40px !important;
    }

    .eta {
        text-align: right;
    }

    .operational {
        color: $green;
    }

    .non-operational {
        color: $red;
    }

    .departed {
        color: $blue;
    }

    .arriving {
        color: $green;
    }

    .near {
        color: $red;
    }
}

.loader {
    @include centering-the-unknown();

    height: 50px !important;
    margin: 0 auto 20px;
    width: 50px !important;
}